.order {
  width: 328px;
  border-radius: 1rem;
  overflow: hidden;
}
@media (min-width: 40em) {
  .order {
    width: 450px;
  }
}
.order__image {
  border-top-right-radius: 16px;
  border-top-left-radius: 16px;
  display: block;
  max-width: 100%;
}
.order__container {
  background-color: var(--white);
  padding: 1.375rem;
}
@media (min-width: 40em) {
  .order__container {
    padding: 2.125rem;
  }
}
.order__title {
  color: var(--dark-blue);
  text-align: center;
}
.order__description {
  color: var(--desaturated-blue);
  font-size: 13px;
  padding: 0 0.5rem;
  text-align: center;
}
.order__detail {
  align-items: center;
  border-radius: 16px;
  display: flex;
  margin-bottom: 1.5rem;
  padding: 1rem;
  background-color: var(--very-pale-blue);
  justify-content: space-between;
}
.order__type {
  align-items: center;
  display: flex;
}
.order__type ul {
  margin: 0;
}
.order__type--icon {
  margin-right: 16px;
  height: 48px;
}
.order__type--cycle {
  font-size: 14px;
  font-weight: 700;
  color: var(--dark-blue);
  margin-bottom: 3px;
}
.order__type--value {
  color: var(--desaturated-blue);
  font-size: 13px;
}
.order__change, .order__change:active, .order__change:visited {
  color: var(--bright-blue);
  text-decoration: underline;
  font-size: 14px;
  font-weight: 700;
}
.order__change:hover {
  color: var(--desaturated-blue);
}
.order__button {
  background: var(--bright-blue);
  border: 0;
  border-radius: 12px;
  color: var(--white);
  display: block;
  font-weight: 700;
  margin-bottom: 1.5rem;
  padding: 16px 0;
  width: 100%;
  /* offset-x | offset-y | blur-radius | spread-radius | color */
  box-shadow: 5px 17px 30px -8px var(--desaturated-blue);
}
.order__button:hover {
  opacity: 0.6;
}
.order__cancel {
  color: var(--desaturated-blue);
  display: block;
  font-weight: 700;
  text-align: center;
}
.order__cancel:hover {
  color: var(--dark-blue);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-color: var(--background);
  background-image: url("../images/pattern-background-mobile.svg");
  background-repeat: no-repeat;
  background-size: contain;
  font-family: var(--font-red-hat);
  font-size: 16px;
  margin: 0;
  min-height: calc(100vh - 0.1px);
  padding: 0;
}
@media (min-width: 40em) {
  body {
    background-image: url("../images/pattern-background-desktop.svg");
  }
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.attribution {
  margin-top: 0.5rem;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(245, 75%, 52%);
  font-weight: 700;
}

:root {
  --pale-blue: hsl(225, 100%, 94%);
  --bright-blue: hsl(245, 75%, 52%);
  --very-pale-blue: hsl(225, 100%, 98%);
  --desaturated-blue: hsl(224, 23%, 55%);
  --dark-blue: hsl(223, 47%, 23%);
  --white: hsl(0, 0%, 100%);
  --background: var(--pale-blue);
}

:root {
  --font-red-hat: "Red Hat Display", sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

p {
  font-size: 1rem;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}